//for each card
const RestuarantCard = ({
    name,
    cuisines,
    cloudinaryImageId,
    avgRating,
}) => {
    return (
        <div className="card">
            <img src={"https://res.cloudinary.com/swiggy/image/upload/fl_lossy,f_auto,q_auto,w_508,h_320,c_fill/" 
            + cloudinaryImageId}/>
            <h2>{name}</h2>
            <h3>{cuisines.join(", ")}</h3>
            <h4>⭐{avgRating}</h4>
        </div>
    );
};

const Body = () => {
    return (
        <div className="restaurant-list">
            {
                restuarantList.map((restaurant) => {
                    return <RestuarantCard {...restaurant.data} key={restaurant.data.id}/>;
                })
            }
        </div>        
    );
};

/* 
Parent of all JSX
    - Header
    - Body
    - Footer
*/
const AppLayout = () => {
    return (
        // React.Fragment
        <> 
            <Header/>
            <Body/>
            <Footer/>
        </>
    );
};

const Footer = () => {
    return (
        <h1>Footer</h1>
    );
}; 


const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(<AppLayout/>);